<template>
    <div class="page-index verifyformZj">
        <van-nav-bar            
            left-arrow
            title="我的认证"
            @click-left="onClickLeft"
        >    
        </van-nav-bar>
        <div class="main">
            <div class="item item_1" >
                <p class="label label_1" >认证类型</p>
                <ul class="verif_type" v-if="type == 1">
                    <li :class="{'active':data.occupation == '行业专家'}"  >行业专家</li>
                    <li :class="{'active':data.occupation == '经销商'}" >经销商</li>
                    <li :class="{'active':data.occupation == '汽车厂商'}">汽车厂商</li>
                </ul>
                <ul class="verif_type" v-if="type == 2">
                    <li :class="{'active':editData.occupation == '行业专家'}"  @click="handleVerifType('行业专家')" >行业专家</li>
                    <li :class="{'active':editData.occupation == '经销商'}" @click="handleVerifType('经销商')">经销商</li>
                    <li :class="{'active':editData.occupation == '汽车厂商'}" @click="handleVerifType('汽车厂商')">汽车厂商</li>
                </ul>                
            </div>
            <van-divider class="line" />
            <div class="item item_2 flex  ">
                 <p class="label flex-none" >姓名</p>
                 <p class="value" v-if="type == 1">{{data.realname}}</p>
                 <van-field  v-if="type == 2" v-model="editData.realname" placeholder="请输入您的姓名" right-icon="cross" @click-right-icon="editData.realname = ''" />
            </div>
            <van-divider class="line" />
            <div class="item item_3 item_image">
                <p class="label">名片或工作证<span  v-if="type == 2">（横向拍摄识别更精确）</span></p>
                <div class="value" v-if="type == 1">
                    <img :src="data.base_url" v-if="data.base_url" >
                </div> 
                <van-uploader    :after-read="afterRead" v-if="type == 2" >
                    <div class="image-box">
                        <img :src="editData.base_url" v-if="editData.base_url"  >
                    </div>
                </van-uploader>
            </div>
            <van-divider class="line" />
            <div class="item item_2  flex ">
                 <p class="label flex-none" >联系电话</p>
                 <p class="value" v-if="type == 1" >{{data.mobile}}</p>
                 <van-field  v-if="type == 2" v-model="editData.mobile" placeholder="请输入您的联系电话" right-icon="cross" @click-right-icon="editData.mobile = ''" />
            </div>
            <van-divider class="line" />
            <div class="item item_2  ">
                 <p class="label" >在职公司名称</p>
                 <p class="value" v-if="type == 1" >{{data.company}}</p>
                 <van-field  v-if="type == 2" v-model="editData.company" placeholder="请输入您的在职公司名称" right-icon="cross" @click-right-icon="editData.company = ''" />
            </div>
            <van-divider class="line" />
            <div class="item item_2  ">
                 <p class="label" >职业或者某领域的专家</p>
                 <p class="value" v-if="type == 1" >{{data.position}}</p>
                 <van-field  v-if="type == 2" v-model="editData.position" placeholder="请输入您的专家职位" right-icon="cross" @click-right-icon="editData.position = ''" />
            </div>
            <van-divider class="line" />
            <div class="btn-box flex">
                <div class="btn-action" @click="handleAction">
                    <span>{{type == 1 ? '修改资料' :'取消修改'}}</span>
                </div>
                <div class="btn-status">
                    <div class="" v-if="type == 1">
                        <span v-if="data.flag == 0">审核中...</span>
                        <span v-if="data.flag == 1">认证失败</span>
                        <span v-if="data.flag == 2">已认证</span>
                    </div>
                    <div class="" v-if="type == 2" @click="checkForm()">
                        <span>重新审核</span>
                    </div>
                </div>
            </div>
        </div>
      
    </div>
</template>

<script>
import { Toast } from 'vant';
import {deepCopy} from '@js/utils'
export default {
    props:['itemdata'],
    data(){
        return {
            type:1,    // 1 : 浏览 2 : 编辑
            data:{
                vid:'',
                company:'',
                mobile:'',
                occupation:'行业专家',
                realname:'',
                position:'',
                job_image:'',
                flag:0,
                base_url:'',
                token:this.$store.state.userData
            },
            editData:null,
        }
    },
    created(){
        this.getIndustryVerify();
    },
    mounted(){

    },
    beforeRouteEnter (to, from, next) {
        next( v =>{
            v.init();
        });
    },
    methods:{
        init(){
            if(this.$route.query.type == 'edit'){
                this.data.vid = this.itemdata.vid
                this.data.mobile = this.itemdata.mobile;
                this.data.realname = this.itemdata.realname;
                this.data.myfile = this.itemdata.myfile;
                this.data.base64 = this.itemdata.real_myfile
            }
        },
        afterRead(file){
            let imageformData = new FormData();
            imageformData.append('token',this.$store.state.userData);
            imageformData.append('myfile',file.file);
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            this.api.addVerifyImage(imageformData).then(res => {
                Toast.clear();
                if(res.err_code == 0){
                    this.editData.base_url = res.data.real_file_url
                    this.editData.job_image = res.data.file_url
                }else{
                    Toast(res.err_msg);  
                }
            })
            // this.editData.base64 = file.content;
            // this.editData.myfile = file.file;
  
        }, 
        // 选择认证类型
        handleVerifType(v){
            this.editData.occupation = v;
        },  
        handleAction(){
            if(this.data.flag == 2){
                return Toast('认证通过的资料不可修改!')
            }
            if(this.type == 1){
                this.fillForm();
                this.type = 2;
            }else if(this.type == 2){
                this.type = 1;
            }
        },      
        onClickLeft(){
            this.$router.go(-1)
        },
        // 编辑回填
        fillForm(){
            this.editData = deepCopy(this.data);
        },
        checkForm(){
            if(!this.editData.occupation){
                return Toast('请选择认证类型')
            }
            if(!this.editData.realname){
                return Toast('请输入您的姓名')
            }
            if(!this.editData.base_url){
                return Toast('请上传名片或者工作证')
            }
            if(!this.editData.mobile){
                return Toast('请输入您的联系电话')
            }
            if(!this.editData.company){
                return Toast('请输入您的在职公司名称')
            }
            if(!this.editData.position){
                return Toast('请输入您的专家职位')
            }
            this.hadnleSubmit();
        },
        // 获取行业专家认证 信息
        getIndustryVerify(){
            this.api.getIndustryVerify({
                token:this.$store.state.userData
            }).then( res => {
                if(res.err_code == 0){
                    if(JSON.stringify(res.data) != '[]'){
                        let {company,position,realname,job_image,occupation,mobile,vid,flag,base_url} = res.data;
                        this.data.vid = vid;
                        this.data.company =company;
                        this.data.mobile = mobile;
                        this.data.occupation = occupation;
                        this.data.realname = realname;
                        this.data.position = position;
                        this.data.job_image = job_image;
                        this.data.flag = flag;
                        this.data.base_url = base_url;
                    }

                }
            })
        },
        hadnleSubmit(){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            let formData = new FormData();
            for(let k  in this.editData ){
                if(k != 'flag' && k != 'base_url'){

                    formData.append(k,this.editData[k])
                }
            };
            this.api.addIndustryVerify(formData).then(res => {
                Toast.clear();
                if(res.err_code == 0){
                    Toast('修改成功')
                    this.getIndustryVerify();
                    this.type = 1;
                }else {
                    Toast(res.err_msg);
                }
            })
        },
    }
}
</script>

